import {Link} from 'react-router-dom'
import PropTypes from 'prop-types'
import {useState} from 'react'

const Navbar = (props) => {

    const [title, setTitle] = useState(props.title);
    
    return (
        <div className="navbar">
            <div className="dropbtn">
                <div></div>
                <div></div>
                <div></div>
            </div>
            <div className="pageTitle">Title</div>
            <div className="dropdown-content">
                <Link to='Setup' onClick={()=>(console.log('x'))}>Setup</Link>
                <Link to='Game' onClick={()=>(console.log('x'))}>Game</Link>
                <Link to='Scoreboard' onClick={()=>(console.log('x'))}>Scoreboard</Link>
            </div>
        </div>

    )

    function toggleNav(){
        const navContent = document.querySelector('.navbarContent');
        navContent.classList.toggle('showNavigation');
    }
}





export default Navbar
